





















import { Vue, Component, Prop } from 'vue-property-decorator'
import VuiTransition from '../transition'
import VuiOverlayer from '../overlayer'

@Component({
  name: 'VuiDialog',
  inheritAttrs: false,
  components: {
    VuiTransition,
    VuiOverlayer
  }
})
export default class VComponent extends Vue {
  @Prop({
    type: Boolean,
    default: false
  })
  value!: boolean

  // 是否显示关闭按钮
  @Prop({
    type: Boolean,
    default: false
  })
  showClose!: boolean

  @Prop({
    type: String,
    default: ''
  })
  title!: string
}
